<template>
    <div class="Home">
        <div>
            <img  v-for="item in models" :src="item.img" style="width: 80px; height: 80px"
                 @click="selectModel(item)">
        </div>
        <div id="canvas">
        </div>
    </div>
</template>

<script>
  import '../lib/threejs/three.js';
  import '../lib/threejs/controls/DragControls.js'
  import '../lib/threejs/controls/OrbitControls.js'
  import '../lib/threejs/controls/TrackballControls.js'
  import '../lib/threejs/loaders/GLTFLoader.js'
//import * as THREE from 'three';
//import { Scene } from 'three';
//import { TrackballControls } from 'three';
//import { TrackballControls } from 'three/examples/jsm/controls/TrackballControls.js';
//import { DragControls } from 'three';
//import { DragControls } from 'three/examples/jsm/controls/DragControls.js';
//import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
//import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
//  import '../lib/threejs/libs/stats.min.js'

  let loader = new THREE.GLTFLoader();
  let stats;
  let camera, controls, scene, renderer;
  let objects = [];
  export default {
    name: 'Home',
    data () {
      return {
        scene: null,
        models: [
          {
            id: '',
            img: require('../assets/models/gltf/DamagedHelmet/glTF/Default_albedo.jpg'),
            gltf: './static/models/gltf/DamagedHelmet/glTF/DamagedHelmet.gltf'
          },
          {
            id: '',
            img: require('../assets/models/gltf/Duck/glTF/DuckCM.png'),
            gltf: './static/models/gltf/Duck/glTF/Duck.gltf'
          }
        ]
      }
    },

    mounted(){
      this.init();

      document.addEventListener('mousedown', function (e) {
          controls.enabled = e.target.nodeName === 'CANVAS';
      })

      document.addEventListener('mousemove', function (e) {
        controls.enabled = e.target.nodeName === 'CANVAS';
      })

      this.animate();
    },

    methods: {
      init() {
        this.scene = new THREE.Scene();
        this.scene.background = new THREE.Color(0xf0f0f0);

        camera = new THREE.PerspectiveCamera(70, window.innerWidth / window.innerHeight, 0.01, 1000);
        camera.position.z = 4;

        renderer = new THREE.WebGLRenderer({antialias: true});
        renderer.setPixelRatio(window.devicePixelRatio);
        renderer.setSize(window.innerWidth, window.innerHeight);
        //
        camera.position.x = -30;
        camera.position.y = 20;
        camera.position.z = 10;
        camera.lookAt(this.scene.position)
        document.querySelector('#canvas').appendChild(renderer.domElement);

        //
        var axes = new THREE.AxisHelper(20);
        this.scene.add(axes);

        controls = new THREE.TrackballControls(camera);
//        controls.rotateSpeed = 1.0;
//        controls.zoomSpeed = 1.2;
//        controls.panSpeed = 0.8;
//        controls.noZoom = false;
//        controls.noPan = false;
//        controls.staticMoving = true;
//        controls.dynamicDampingFactor = 0.3;
//        controls.target.set( 0, - 0.2, - 0.2 );

        controls.rotateSpeed = 1.0;
        controls.zoomSpeed = 1.0;
        controls.panSpeed = 1.0;
        controls.noZoom = false;
        controls.noPan = false;
        controls.staticMoving = true;
        controls.dynamicDampingFactor = 0.3;
        controls.update();

        var planeGeometry = new THREE.PlaneGeometry(40, 20);
        //var planeMaterial = new THREE.MeshBasicMaterial({color:0xcccccc});
        var planeMaterial = new THREE.MeshLambertMaterial({color: 0xffffff, opacity: 0.2});//
        var plane = new THREE.Mesh(planeGeometry, planeMaterial);
        plane.rotation.x = -0.5 * Math.PI;
        plane.position.x = 15;
        plane.position.y = 0;
        plane.position.x = 0;
        this.scene.add(plane);
        plane.receiveShadow = true;

        let ambientLight = new THREE.AmbientLight(0xcccccc, 0.4);
        this.scene.add(ambientLight);

        let pointLight = new THREE.PointLight(0xffffff, 0.8);
        camera.add(pointLight);
        this.scene.add(camera);

        let dragControls = new THREE.DragControls(objects, camera, renderer.domElement);
        dragControls.addEventListener('dragstart', function (event) {
          controls.enabled = false;
        });
        dragControls.addEventListener('dragend', function (event) {
          controls.enabled = true;
        });
//				stats = new Stats();
//				document.body.appendChild( stats.dom );
        window.addEventListener('resize', this.onWindowResize, false);
      },

      selectModel(item){
        loader.load(item.gltf, (gltf) => {
          this.scene.add(gltf.scene);
          gltf.scene.traverse(function (object) {
            if (object.isMesh){
              objects.push(object);
            }
          });
        });
      },

      onWindowResize() {
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(window.innerWidth, window.innerHeight);
      },
      //
      animate() {
        requestAnimationFrame(this.animate);
        this.render();
//				stats.update();
      },

      render() {
        controls.update();
        renderer.render(this.scene, camera);
      }
    }
  }
</script>

<style scoped>
    h1, h2 {
        font-weight: normal;
    }

    ul {
        list-style-type: none;
        padding: 0;
    }

    li {
        display: inline-block;
        margin: 0 10px;
    }

    a {
        color: #42b983;
    }
</style>



